.std-banner {
  position: relative;
}

.std-banner__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  inset: 0;
}

.std-banner__overlay {
  width: 100%;
  height: 100%;
  background: #00000080;
  position: absolute;
  inset: 0;
}

.std-banner__text {
  color: #fff;
  text-align: center;
  position: relative;
}

